olute;top:0;left:0;height:100%}.tunnel__box__lt .spotlight__inner__light{-webkit-clip-path:polygon(0 179px, 100% 0, 100% 100%, 0 220px);clip-path:polygon(0 179px, 100% 0, 100% 100%, 0 220px);position:absolute;left:48px}.tunnel__box__lt .spotlight__inner{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}.spotlight__inner__light{width:400px;height:400px}.spotlight__inner__light__bg{width:100%;height:100%;position:relative}.tunnel__box__rt{position:absolute;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.tunnel__box__rt svg *{fill:#8306b1}.tunnel__box__rt svg{filter:drop-shadow(1px 0px 12px #8306b1);-webkit-filter:drop-shadow(1px 0px 12px #8306b1);-moz-filter:drop-shadow(1px 0px 12px #8306b1)}.tunnel__box__rt .spotlight__inner__light{-webkit-clip-path:polygon(0 0, 100% 180px, 100% 221px, 0 100%);clip-path:polygon(0 0, 100% 180px, 100% 221px, 0 100%);position:absolute;left:-400px}.tunnel__box__rt svg{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.tunnel__box__rt .spotlight__inner{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.tunnel__box__rt .spotlight__inner__light__bg:after{width:100%;height:100%;content:"";display:block;position:absolute;top:0;left:0;height:100%}

